import {Avatar, Box, Stack, StackProps} from "@mui/material";
import HiveIcon from "@mui/icons-material/Hive";
import React from "react";
import Divider from "@mui/material/Divider";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import MarkdownArea from "../../../component/text/MarkdownArea";
import CopyButton from "../../../component/button/CopyButton";
import {AiChatRecordMessage} from "../../../proto/application/aichat/AiChatMessage";
import {Typography} from "../../../component/wrapper/MuiWrappers";

type ChatRecordProps = {
  record: AiChatRecordMessage
} & StackProps
const ChatRecord = ({record, sx, ...rest}: ChatRecordProps) => {
  return <><Stack direction={"row"}
                  paddingY={2}
                  maxWidth={"100%"}
                  sx={{...sx}}
                  {...rest}>
    <Box marginTop={1}>
      {record.role === "assistant" ? <Avatar sx={{bgcolor: "primary.main"}}
                                             style={{backgroundColor: "#e4a710"}}><HiveIcon/></Avatar> :
          <Avatar sx={{bgcolor: "primary.main"}}><AccountCircleIcon/></Avatar>}
    </Box>
    <Stack direction={"column"} marginX={4} textAlign={"start"}>
      <Typography variant={"caption"}>
        {new Date(record.createAt).toLocaleString()} {record.role === "assistant" ? `(${record.model})` : ""}
      </Typography>
      <Stack direction={"row"} flexGrow={1} textAlign={"start"}>
        <MarkdownArea>
          {record.message}
        </MarkdownArea>
        <CopyButton sx={{alignSelf: "flex-start", marginTop: 1}} text={rest.children as string}/>
      </Stack>

    </Stack>

  </Stack>
    <Divider sx={{width: "100%"}}/>
  </>;
}


export default React.memo(ChatRecord);