import {pb_pbPost, str_pbPost, str_strPost} from "../../../api/util/MyDimensionClient";
import {PageQuery} from "../../../proto/framework/QueryMessage";
import {
  AiChatRecordMessage,
  AiChatThreadMessage,
  AiChatThreadMessageList,
  TalkToAiRequest
} from "../../../proto/application/aichat/AiChatMessage";

const aiChatAPIPrefix = '/api/v0/ai/chat';

export const getThreadProfile = (page: number, pageSize: number) => {
  const pageQuery = PageQuery.fromJSON({page: page, pageSize: pageSize});

  return pb_pbPost(aiChatAPIPrefix + "/getThreadProfile", PageQuery, AiChatThreadMessageList, pageQuery);
}

export const getChatThread = (threadId: string) => {
  return str_pbPost(aiChatAPIPrefix + "/getChatThread", AiChatThreadMessage, threadId);
}

export const newChatThread = () => {
  return str_strPost(aiChatAPIPrefix + "/newChatThread");
}

export const setVisibility = (isPublic: boolean, threadId: string) => {
  if (isPublic) {
    return str_strPost(aiChatAPIPrefix + "/make_public", threadId);
  } else {
    return str_strPost(aiChatAPIPrefix + "/make_private", threadId);
  }
}

export const talkToAssistant = (message: string, threadId: string, model: string) => {
  const talkRequest = TalkToAiRequest.fromJSON({
    message: message,
    threadId: threadId,
    model: model
  });

  return pb_pbPost(aiChatAPIPrefix + "/talk_sync", TalkToAiRequest, AiChatRecordMessage, talkRequest);
}