// @flow
import * as React from 'react';
import {useCallback, useEffect, useRef} from 'react';
import {
  Autocomplete,
  CircularProgress,
  Divider,
  Drawer,
  IconButton,
  MenuList,
  Stack,
  Switch
} from "@mui/material";
import {Button, FormControlLabel, TextField, Typography} from "../../component/wrapper/MuiWrappers";
import {
  getChatThread,
  getThreadProfile,
  newChatThread,
  setVisibility,
  talkToAssistant
} from "./service/AiChatService";
import AddIcon from '@mui/icons-material/Add';

import {AutocompleteRenderInputParams} from "@mui/material/Autocomplete/Autocomplete";
import {print} from "../../api/util/Logging";
import SendIcon from '@mui/icons-material/Send';
import HistoryIcon from '@mui/icons-material/History';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import {useTranslation} from "react-i18next";
import {
  AiChatRecordMessage,
  AiChatThreadMessage
} from "../../proto/application/aichat/AiChatMessage";
import ChatRecord from "./component/ChatRecord";
import {useSearchParams} from "react-router-dom";
import BreakpointContainer from "../../component/container/BreakpointContainer";
import AlertContext from "../../context/AlertContext";
import {ThreadProfileItem} from "./component/ThreadProfileItem";
import ListComponent from "../../component/container/ListComponent";
import FunctionalTextArea from "../../component/input/FunctionalTextArea";

// https://platform.openai.com/docs/models/gpt-4
const allModels = [
  "gpt-4o",
  "gpt-4o-mini",
  "gpt-4-turbo",
  "gpt-4",
  "gpt-3.5-turbo",
  "gpt-3.5-turbo-16k"];

export const AiChatRoom = () => {
  const {t} = useTranslation();
  const [newMessage, setNewMessage] = React.useState<string>("");
  const {showAlert} = React.useContext(AlertContext)
  const [searchParams, setSearchParams] = useSearchParams();
  const threadId = searchParams.get("id");

  const [messages, setMessages] = React.useState<AiChatRecordMessage[]>([]);
  const [loading, setLoading] = React.useState<boolean>(false);
  const [currentModel, setCurrentModel] = React.useState<string>(allModels[0]);
  const [threadProfiles, setThreadProfiles] = React.useState<AiChatThreadMessage[]>([]);
  const [maxPage, setMaxPage] = React.useState<number>(0);
  const [currentThreadProfilePage, setCurrentThreadProfilePage] = React.useState<number>(0);
  const [leftDrawerState, setLeftDrawerState] = React.useState(false);
  const [isPublic, setIsPublic] = React.useState<boolean>(false);
  const pageSize = 10;


  const bottomRef = useRef<null | HTMLDivElement>(null);
  const inputRef = useRef<null | HTMLDivElement>(null);

  useEffect(() => {
    if (!loading) {
      inputRef.current?.focus();
    }
  }, [loading]);

  useEffect(() => {
    getThreadProfile(currentThreadProfilePage, pageSize).then((response) => {
      setThreadProfiles(response.threads);
      if (response.pageResult) {
        setMaxPage(response.pageResult.totalPage - 1);
      }
    }).catch((error) => {
      showAlert(error.message, "error");
    });
  }, []);

  useEffect(() => {
    if (threadId) {
      getChatThread(threadId).then((response) => {
        if (response) {
          setMessages(response.messages);
          setIsPublic(response.isPubic);
        }
      }).catch((error) => {
        showAlert("Chat not found", "error");
        setSearchParams({});
        print(error);
      });
    } else {
      setMessages([]);
    }
  }, [searchParams]);

  useEffect(() => {
    // ðŸ‘‡ï¸ scroll to bottom every time messages change
    bottomRef.current?.scrollIntoView({behavior: 'smooth', block: 'end'});

  }, [messages]);

  const loadMoreThreadProfile = () => {
    if (currentThreadProfilePage < maxPage) {
      getThreadProfile(currentThreadProfilePage + 1, pageSize).then((response) => {
        setThreadProfiles([...threadProfiles, ...response.threads]);
        setCurrentThreadProfilePage(currentThreadProfilePage + 1);
        if (response.pageResult) {
          setMaxPage(response.pageResult.totalPage - 1);
        }
      }).catch((error) => {
        showAlert(error.message, "error");
      });
    }
  }

  const sendMessage = () => {
    if (newMessage === "") {
      return;
    }
    setLoading(true);
    const newMessageRecord = AiChatRecordMessage.create();
    newMessageRecord.message = newMessage;
    newMessageRecord.role = "user";
    newMessageRecord.createAt = Date.now();

    setMessages((oldMessages) => [...oldMessages, newMessageRecord]);
    if (!threadId) {
      newChatThread().then((response) => {
        const newThreadId = response;
        talkToAssistant(newMessage, response, currentModel).then((response) => {
          setMessages((oldMessages) => [...oldMessages, response]);
          setSearchParams({id: newThreadId});
          setLoading(false);
          setNewMessage("");
        });
      });
    } else {
      talkToAssistant(newMessage, threadId, currentModel).then((response) => {
        setMessages((oldMessages) => [...oldMessages, response]);
        setLoading(false);
        setNewMessage("");
      });
    }
  }

  const checkKeyPress = useCallback(async (e: KeyboardEvent) => {
    if (e.key === 'Enter') {
      if (e.ctrlKey || e.shiftKey) {
        e.preventDefault();
        sendMessage();
      }
    }
  }, [newMessage]);


  useEffect(() => {
    window.addEventListener("keydown", checkKeyPress);
    return () => {
      window.removeEventListener("keydown", checkKeyPress);
    };
  }, [checkKeyPress]);

  return <Stack direction={"row"} justifyContent={"start"} alignItems={"start"} spacing={0}
                width={"100%"}
                height={"89dvh"}>
    <BreakpointContainer up={"md"}>
      <Stack direction={"column"}
             alignItems={"flex-start"}
             height={"100%"}
             width={"20%"}
             minWidth={"250px"}
             spacing={0}
             sx={{
               paddingX: "5px",
               overflowY: "auto",
               textOverflow: "ellipsis",
               backgroundColor: "#f1f1f1",
               borderRadius: "5px"
             }}>
        <Stack direction={"row"} justifyContent={"center"} width={"100%"} paddingTop={2}>
          <Typography variant={"h6"}>{"Chat History"}</Typography>
        </Stack>

        <MenuList dense sx={{width: "100%"}}>
          <ListComponent data={threadProfiles} renderItem={(profile) => {
            return <Stack key={profile.threadId} direction={"column"}>
              <ThreadProfileItem key={profile.threadId} profile={profile}
                                 onClick={() => {
                                   setSearchParams({id: profile.threadId});
                                 }}/>
              <Divider/>
            </Stack>
          }}/>
        </MenuList>

        {(currentThreadProfilePage < maxPage) &&
            <Button onClick={loadMoreThreadProfile}>{"Load More"}</Button>}
      </Stack>
    </BreakpointContainer>

    <BreakpointContainer down={"md"}>
      <Drawer
          anchor={"left"}
          open={leftDrawerState}
          onClose={() => setLeftDrawerState(false)}
          className={"blurDialog"}
      >
        <Stack direction={"column"}
               alignItems={"flex-start"}
               width={"70dvw"}
               spacing={0}
               sx={{padding: "20px", overflowY: "auto", textOverflow: "ellipsis"}}>
          <Stack direction={"row"} justifyContent={"center"} width={"100%"}>
            <Typography variant={"h6"} flexGrow={1}>{"Chat History"}</Typography>
            <IconButton
                sx={{marginRight: "8px"}}
                onClick={() => {
                  setLeftDrawerState(false);
                }}>
              <ArrowBackIcon/>
            </IconButton>
          </Stack>
          <ListComponent data={[...threadProfiles]} renderItem={(profile) => {
            return <Stack key={profile.threadId} direction={"column"}
                          width={"100%"}><ThreadProfileItem profile={profile}
                                                            onClick={() => {
                                                              setSearchParams({id: profile.threadId});
                                                              setLeftDrawerState(false);
                                                            }}/>
              <Divider/>
            </Stack>
          }}/>
          {(currentThreadProfilePage < maxPage) &&
              <Button onClick={loadMoreThreadProfile}>{"Load More"}</Button>}
        </Stack>
      </Drawer>
    </BreakpointContainer>


    <Stack direction={"column"} alignItems={"flex-start"} sx={{
      width: {
        xs: "100%",
        md: "85%"
      },
      height: "100%",
    }}>
      <Stack direction={"row"} justifyContent={"start"} alignItems={"center"} width={"100%"}
             spacing={2}>
        <BreakpointContainer down={"md"}>
          <IconButton aria-label={"menu"} onClick={() => {
            setLeftDrawerState(true);
          }
          }
                      sx={{justifySelf: "flex-start"}}>
            <HistoryIcon/>
          </IconButton>
        </BreakpointContainer>
        <Autocomplete options={allModels}
                      sx={{
                        flexGrow: 1
                      }}
                      value={currentModel}
                      onChange={(event, newValue) =>
                          setCurrentModel(newValue as string)
                      }
                      renderInput={(params: AutocompleteRenderInputParams) =>
                          <TextField {...params}
                                     label={t("Model")}/>}/>
        {(threadId !== null && threadId.length > 0) &&
            <FormControlLabel control={<Switch checked={isPublic}
                                               onChange={(event) => {
                                                 const toCheck = event.target.checked;
                                                 setVisibility(toCheck, threadId).then((response) => {
                                                   setIsPublic(toCheck);
                                                   if (toCheck) {
                                                     showAlert("You can send the link of this chat to other people to let them join this chat!", "success");
                                                   }
                                                 }).catch((error) => {
                                                   showAlert(error.message, "error");
                                                 });
                                               }}

            />} label="Sharing"/>}
        <IconButton onClick={() => {
          setSearchParams({});
          setMessages([]);
          setCurrentThreadProfilePage(0);

          getThreadProfile(0, pageSize).then((response) => {
            setThreadProfiles(response.threads);
            if (response.pageResult) {
              setMaxPage(response.pageResult.totalPage - 1);
            }
          }).catch((error) => {
            showAlert(error.message, "error");
          });
          inputRef.current?.focus();
        }
        }><AddIcon/></IconButton>
      </Stack>

      <Stack direction={"column"} alignItems={"flex-start"} flexGrow={1}
             sx={{
               width: "100%"
             }}
             overflow={"auto"} textOverflow={"ellipsis"} className={"hideScrollBar"}>

        <ListComponent data={messages} renderItem={(record, index) => {
          return <ChatRecord key={index} record={record}
                             paddingX={2}/>;
        }}/>
        <div ref={bottomRef}/>

      </Stack>

      <Stack direction={"row"}
             width={"100%"}
             justifyContent={"center"} alignItems={"flex-end"} spacing={2}>
        <FunctionalTextArea minRows={3}
                            value={newMessage}
                            inputSlotProps={{sx: {borderRadius: "0px"}}}
                            style={{borderRadius: "0px"}}
                            onChange={(event) => {
                              setNewMessage(event.target.value);
                            }}
                            placeholder={t("Ctrl + Enter to Send a message")}
        >
          <IconButton
              disabled={newMessage.length === 0 || loading}
              onClick={sendMessage}>
            {loading ? <CircularProgress size={24}/> : <SendIcon/>}
          </IconButton>
        </FunctionalTextArea>
      </Stack>
    </Stack>

  </Stack>;
};

export default React.memo(AiChatRoom);