// @flow
import * as React from 'react';
import {AiChatThreadMessage} from "../../../proto/application/aichat/AiChatMessage";
import {MenuItemProps} from "@mui/material/MenuItem";
import {ListItemText, MenuItem} from "../../../component/wrapper/MuiWrappers";

type ThreadProfileItemProps = {
  profile: AiChatThreadMessage
} & MenuItemProps;
export const ThreadProfileItem = ({profile, ...rest}: ThreadProfileItemProps) => {
  return (
      <MenuItem
          key={profile.threadId}
          sx={{
            width: "100%",
            textAlign: "start",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            overflow: "hidden",
            minHeight: "2rem"
          }} {...rest}> <ListItemText
          enableTranslate={false}>{profile.title}</ListItemText></MenuItem>
  );
};